*{
   
    font-family: sans-serif;
    
}
body{
    color: #ffbd00;
    background-color: #0a3a49;
    padding: 0;
    margin: 0;
    
    
    
}
.todo-container{
    
    width: 350px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    
}
.input-section{
    background-color: #728185;
    padding: 18px;
    border-radius: 5px;
    background-color: #092d38;
    

}
.input-section input{
    border-radius: 5px;
    border: 1px solid silver;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    background-color: #ababab;
    outline: none;
}
h1{
    margin: 0;
    margin-bottom: 15px;
    color: #ababab;
}

ul{
    
    text-align: left;
    padding: 0;

}
ul li{
    list-style: none;
    background-color: #092d38;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 10px;
    color: #ababab;
    box-shadow: 0, 4px, 6px, #00000066;
    display: flex;
}
ul li i{
    text-align: right;
    flex: 1;
    cursor: pointer;
}